  <div id="main" style="margin-top: 15px">
    <div id="left">
      <div style="display: flex; flex-direction: row; justify-content: space-between">
        <p>Current points:</p>
        <p id="current" >(0,0)</p>
        <div class="button-group">
          <button class="button" id="back-button">Back</button>
        </div>
      </div>

      <div id="simple-content" style="display: block">

        <p>Enter X: </p>

        <label style="display: flex; justify-content: center">
          <textarea  v-model="x_data" id="x_value" rows="1" cols="5" maxlength="5" style="margin-left: 0px; margin-top: 5px;" required autofocus ></textarea>
        </label>
        <p>Select Y: </p>
        <div class="y-button-line">
          <input type="radio" id="y_-2" name="y_value" value="-2" v-model="y_data">
          <label for="y_-2">-2</label>
          <input type="radio" id="y_-1.5" name="y_value" value="-1.5" v-model="y_data">
          <label for="y_-1.5">-1.5</label>
          <input type="radio" id="y_-1" name="y_value" value="-1" v-model="y_data">
          <label for="y_-1">-1</label><br>
          <input type="radio" id="y_-0.5" name="y_value" value="-0.5" v-model="y_data">
          <label for="y_-0.5">-0.5</label>
          <input type="radio" id="y_0" name="y_value" value="0" v-model="y_data">
          <label for="y_0">0</label>
          <input type="radio" id="y_0.5" name="y_value" value="0.5" v-model="y_data">
          <label for="y_0.5">0.5</label>
          <input type="radio" id="y_1" name="y_value" value="1" v-model="y_data">
          <label for="y_1">1</label>
          <input type="radio" id="y_1.5" name="y_value" value="1.5" v-model="y_data">
          <label for="y_1.5">1.5</label>
          <input type="radio" id="y_2" name="y_value" value="2" v-model="y_data" checked>
