</template>

<script>
  import JXG from 'jsxgraph';

  export default {
    name: "Query",
    data() {
      return {
        x_data: "",
        y_data: [2],
        r_data: [3]
      }
    },
    mounted() {
        let validate = (x, y) => {
            if(x === ""){
              alert('x can\'t be empty!');
              return false;
            }
            if(x < -4 || x > 4 ) {
              alert('x is out of range!');
              return false;
            }
            if(y < -2 || y > 2){
              alert('y is out of range!');
              return false;
            }
            return true;
          }

        let board = JXG.JSXGraph.initBoard('box1', {
          axis: true,
          boundingbox: [-5.25, 5.25, 5.25, -5.25]
        })

      let r = board.create('slider', [[1, 3], [3, 3], [1, 4, 3]], {name: 'R', snapWidth: 0.25, visible: true});
      let R = () => r.Value();
      let minusR = () => -r.Value();
      let minusHalfR = () => -r.Value() / 2;



      // second corner (triangle)
      board.create('polygon', [[0, 0], [minusR, 0], [0, R]], {
        fillColor: '#e74c3c',
        strokeColor: 'none',
        withLines: false,
        vertices: {visible: false}
      });

//third corner (square)
      board.create('polygon', [[0, 0], [minusHalfR, 0], [minusHalfR, minusR], [0, minusR]], {
        fillColor: '#3498db',
        strokeColor: 'none',
        withLines: false,
        vertices: {visible: false}
      });

//quarter corner (quarter circle)
      let quarterCircle = X => -Math.sqrt((r.Value() - X) * (r.Value() + X));
      board.create('functiongraph', [quarterCircle, 0, R], {
        fillColor: '#16a085',
        fillOpacity: 0.3,
        strokeColor: 'none',
        highlight: false,
        withLines: false,
        vertices: {visible: false},
      });

      board.create('polygon', [[0, 0], [R, 0], [0, minusR]], {
        fillColor: '#16a085',
        strokeColor: 'none',
        withLines: false,
        vertices: {visible: false},
      })

      const getXY = (e, i) => {
        let currentPos = board.getCoordsTopLeftCorner(e, i),
            absolutePos = JXG.getPosition(e, i),
            dx = absolutePos[0] - currentPos[0],
            dy = absolutePos[1] - currentPos[1];

        let jxgCoordinate = new JXG.Coords(JXG.COORDS_BY_SCREEN, [dx, dy], board);
        let x = jxgCoordinate.usrCoords[1].toFixed(2);
        let y = jxgCoordinate.usrCoords[2].toFixed(2);
        return {x, y};
      }

      let drawPoint = (x, y, radius, result) => {
        console.log('drawing...' + x + ' ' + y + ' ' + radius + ' ' + result);
        board.create('point', [x, y], {
          color: result ? 'green' : 'red',
          label: {visible: false}
        });
        let newRow = document.createElement('tr');
        let newX = document.createElement('td');
        newX.innerText = x;
        newRow.appendChild(newX);
        let newY = document.createElement('td');
        newY.innerText = y;
        newRow.appendChild(newY);
        let newR = document.createElement('td');
        newR.innerText = radius;
        newRow.appendChild(newR);
        let newResult = document.createElement('td');
        newResult.innerText = result;
        newRow.appendChild(newResult);
        document.getElementById('table-body').appendChild(newRow);
      }

      const addPoint = (x,y, radius, body) => fetch('/point' + body,{
        method: "POST",
        headers: {
          'Content-Type': 'application/json'
        }
      })
        .then(response => {
            if(response.ok) {
              return response.json();
            }
            else alert('Server is not connected or query is incorrect format!');
        })
        .then(response => {
          console.log(response);
          response.forEach((point) => {
            console.log(point.x);
            console.log(parseFloat(x).toFixed(2));
            if(point.x === parseFloat(x).toFixed(2) &&
              point.y === parseFloat(y).toFixed(2)
            && point.r === parseFloat(radius).toFixed(2)) {
              drawPoint(point.x, point.y, radius, point.result === 'true');
            }
          })
        })
        .catch(error => {
            alert(error);
        });

      function fetchPoints(){
          let information = {
              'login': localStorage.getItem('login')
          }
          let body = [];
          for (const inf in information){
            body.push(inf + '=' + information[inf]);
          }
          body = '?' + body;
          fetch("/all" + body, {
              method: "POST",
              headers: {
              'Content-Type': 'application/json'
              }
          }).then(response => {
              if(response.ok) {
                return response.json();
              }
              else this.$router.back();
          }) .then(response => {

              response.forEach((point) => {
                  drawPoint(point.x, point.y, point.r,point.result === 'true');
              })
          }).catch(error => {
              console.log(error);
          })
      }

      fetchPoints();

      let current = document.getElementById('current');

      document.getElementById('back-button').addEventListener('click', (e, i) => {
        localStorage.removeItem('login');
        this.$router.replace('/')
      });
      document.getElementById('submit-button').addEventListener('click', (e, i) => {
        console.log('x_data: ' + this.x_data);
        console.log('y_data: ' +  this.y_data);
        console.log('r_data: ' + this.r_data);
        if(!validate(this.x_data, this.y_data)) return;
        let information = {
          "login": localStorage.getItem("login"),
          "x": this.x_data,
          "y": this.y_data,
          "r": this.r_data
        }
        let body = [];
        for (const inf in information){
          body.push(inf + "=" + information[inf]);
        }

        console.log(body);
        body = "?" + body.join('&');
        addPoint(this.x_data, this.y_data, this.r_data, body);
      })

      document.getElementById('box1').addEventListener('mousemove', (e, i) => {
        let {x, y} = getXY(e, i);
        current.innerText = ('(' + x + ', ' + y + ')');
      });

      document.getElementById('box1').addEventListener('mousedown', (e, i) => {
          let {x, y} = getXY(e, i);
          if(!validate(x, y)) return;
          if(r.Value() === 1 || r.Value() === 1.5 || r.Value() === 2 || r.Value() === 2.5 || r.Value() === 3){
            let information = {
              "login": localStorage.getItem("login"),
              "x": x,
              "y": y,
              "r": R(),
            }
            let body = [];
            for (const inf in information){
              body.push(inf + "=" + information[inf]);
            }
            console.log(body);
            body = "?" + body.join('&');
            addPoint(x, y, r.Value(), body);
          } else alert('r is out of range!');

      })
      let overviewInput = document.getElementById('overview');
      let constraintInput = document.getElementById('constraint');
      let notationInput = document.getElementById('notation');

      let overviewContent = document.getElementById('overviewContent');
      let constraintContent = document.getElementById('constraintContent');
      let notationContent = document.getElementById('notationContent');

      overviewInput.addEventListener('click', () => {
        if (!overviewInput.classList.contains('checked')) {
          overviewInput.classList.toggle('checked');
          overviewContent.style.display = 'block';
        }
        if (constraintInput.classList.contains('checked')) {
          constraintInput.classList.toggle('checked');
          constraintContent.style.display = 'none';

        }
        if (notationInput.classList.contains('checked')) {
          notationInput.classList.toggle('checked');
          notationContent.style.display = 'none';

        }
      })

      constraintInput.addEventListener('click', () => {
        if (overviewInput.classList.contains('checked')) {
          overviewInput.classList.toggle('checked');
          overviewContent.style.display = 'none';
        }
        if (!constraintInput.classList.contains('checked')) {
          constraintInput.classList.toggle('checked');
          constraintContent.style.display = 'block';

        }
        if (notationInput.classList.contains('checked')) {
          notationInput.classList.toggle('checked');
          notationContent.style.display = 'none';
        }
      })

      notationInput.addEventListener('click', () => {
        if (overviewInput.classList.contains('checked')) {
          overviewInput.classList.toggle('checked');
          overviewContent.style.display = 'none';

        }
        if (constraintInput.classList.contains('checked')) {
          constraintInput.classList.toggle('checked');
          constraintContent.style.display = 'none';

        }
        if (!notationInput.classList.contains('checked')) {
          notationInput.classList.toggle('checked');
          notationContent.style.display = 'block';
        }
      })

    }
  }
</script>

