<template>
  <div>
      <div style="display: flex; flex-direction: row; align-items: center">
        <h3>username</h3>
        <input type="text" style="height: 20px; margin-left: 20px" v-model="username" id="username">
      </div>
      <div style="display: flex; flex-direction: row; align-items: center">
        <h3>password</h3>
        <input type="password" style="height: 20px; margin-left: 20px" v-model="password" id="password">
      </div>
      <div>
        <button style="margin-right: 20px" id="login">Login</button>
        <button id="signup">Signup</button>
      </div>
  </div>
</template>

<script>
export default {
  name: "Index",
  data() {
    return {
      username: "",
      password: ""
    }
  },
  mounted() {
    console.log(this.username);
    console.log(this.password);
    document.getElementById("signup").addEventListener("click", (e, i) => {
        let information = {
          "login": this.username,
          "password": this.password
        };
        let body = [];
        for (const inf in information) {
          body.push(inf + "=" + information[inf]);
        }
        console.log(body);
        body = "?" + body.join('&');
        fetch('/register' + body, {
            method: "POST"
        }).then(response => {
            if(response.ok){
                alert('Signup successfully!')
            }
            else {
              if(response.status === 404) alert('Can\'t connect to server');
              else alert('Account exists!')
            }
        }).catch(error => {
            console.log(error);
        })
    });
    document.getElementById("login").addEventListener("click", (e, i) => {
      let information = {
        "login": this.username,
        "password": this.password
      };
      let body = [];
      for (const inf in information) {
        body.push(inf + "=" + information[inf]);
      }
      console.log(body);
      body = "?" + body.join('&');
      fetch('/login' + body, {
        method: "POST"
      }).then(response => {
        if(response.ok){
          localStorage.setItem('login', this.username);
          this.$router.push({name: 'main-page'});
        } else alert('Login fail!')
      }).catch(error => {
        console.log(error);
      })
    })
  }
}


</script>

